/**
 * Main stylesheet for the theme.
 *
 * Theme styles follow http://cssguidelin.es/ and are organized according to
 * Inverted Triangle CSS (ITCSS) principles put forth by Harry Roberts. In
 * short, each layer of CSS gets more specific as you move down this page.
 */
/*--------------------------------------------------------------
# Settings - Global variables, config switches.
--------------------------------------------------------------*/
/* stylelint-disable declaration-bang-space-before */
/* stylelint-enable */
:root {
	--global-font-size: 1rem;
	--global-baseline: 0.5rem;
	--line-height-heading: 1.25;
	--font-size-cta: 1.125rem;
	--font-size-fp: 2.25rem;
	--font-size-h1: 1.5rem;
	--font-size-h2: 1.25rem;
	--font-size-h3: 1.125rem;
	--font-size-h4: 1rem;
	--font-size-h5: 1rem;
	--font-size-h6: 1rem;
	--font-size-75: 0.75rem;
	--font-size-875: 0.875rem;
	--font-size-125: 1.125rem;
}
@media screen and (min-width: 48em) {
	:root {
		--font-size-cta: 2rem;
		--font-size-fp: 3rem;
		--font-size-h1: 2rem;
		--font-size-h2: 1.5rem;
		--font-size-h3: 1.25rem;
		--font-size-h4: 1.125rem;
		--font-size-h5: 1.125rem;
	}
}
@media screen and (min-width: 62em) {
	:root {
		--font-size-cta: 2.5rem;
	}
}

:root {
	--color-white: #fff;
	--color-black: #000;
	--color-light: #b7a99a;
	--color-base: #007fa3;
	--color-base-rgb: 0, 127, 163;
	--color-light-rgb: 183, 169, 154;
	--color-dark: #111;
	--color-grey-60: rgba(0, 0, 0, 0.6);
	--color-grey-20: rgba(0, 0, 0, 0.2);
	--color-grey-10: rgba(0, 0, 0, 0.1);
}

