html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

/*--------------------------------------------------------------
# Elements - Unclassed HTML elements (type selectors).
--------------------------------------------------------------*/
html {
	color: var(--color-dark);
	font-family: "Titillium Web", sans-serif;
	font-size: 100%;
	line-height: 1.5;
	word-wrap: break-word;
}

button,
input,
optgroup,
select,
textarea {
	font-family: inherit;
	font-size: inherit;
	line-height: inherit;
}

body {
	background: var(--color-white);
}

p {
	margin: 0 0 2rem;
}

blockquote {
	border-left: 0.25rem solid;
	font-style: italic;
	margin: 0 0 2rem;
	padding-left: 1.5rem;
}
blockquote p {
	font-size: 1.25rem;
	line-height: 1.6;
}
blockquote p:last-of-type {
	margin-bottom: 0;
}

button,
input[type=button],
input[type=reset],
input[type=submit] {
	background-color: var(--color-base);
	border: none;
	border-radius: 0;
	color: var(--color-white);
	padding: 0.5rem 1.5rem;
	text-decoration: none;
	transition: all 300ms ease-in-out;
}
button:hover, button:active, button:focus,
input[type=button]:hover,
input[type=button]:active,
input[type=button]:focus,
input[type=reset]:hover,
input[type=reset]:active,
input[type=reset]:focus,
input[type=submit]:hover,
input[type=submit]:active,
input[type=submit]:focus {
	background-color: var(--color-dark);
	color: var(--color-white);
	outline-offset: -2px;
}
button:focus,
input[type=button]:focus,
input[type=reset]:focus,
input[type=submit]:focus {
	outline: thin dotted;
}

textarea {
	display: block;
	min-height: 6.5rem;
}

input,
textarea,
select {
	background-color: rgba(var(--color-light-rgb), 0.3);
	border: 1px solid var(--color-white);
	padding: calc(0.5rem - 2px);
	width: 100%;
}
input:focus,
textarea:focus,
select:focus {
	border-color: var(--color-base);
	box-shadow: 0 0 4px rgba(var(--color-base-rgb), 0.75);
	outline: 2px solid transparent;
}

label {
	display: block;
	font-weight: 600;
}

::placeholder {
	color: var(--color-dark);
}

::-moz-placeholder {
	opacity: 1;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: 600;
	margin: 0 0 1rem;
}

h1,
h1.blocks-rich-text__tinymce.mce-content-body {
	font-size: var(--font-size-h1);
	line-height: var(--line-height-heading);
}

h2,
h2.blocks-rich-text__tinymce.mce-content-body {
	font-size: var(--font-size-h2);
	line-height: var(--line-height-heading);
}

h3,
h3.blocks-rich-text__tinymce.mce-content-body {
	font-size: var(--font-size-h3);
}

h4,
h4.blocks-rich-text__tinymce.mce-content-body {
	font-size: var(--font-size-h4);
}

h5,
h5.blocks-rich-text__tinymce.mce-content-body {
	font-size: var(--font-size-h5);
}

h6,
h6.blocks-rich-text__tinymce.mce-content-body {
	font-size: var(--font-size-h6);
}

hr {
	display: block;
	margin-left: auto;
	margin-right: auto;
	background-color: var(--color-base);
	border: 0;
	height: 2px;
	margin-bottom: 3rem;
	margin-top: 3rem;
	max-width: 7rem;
}

a {
	color: var(--color-base);
	transition: all 300ms ease-in-out;
}
a:hover, a:focus, a:active {
	color: var(--color-dark);
}
a:focus {
	outline: thin dotted;
}
a:hover, a:active {
	outline: 0;
}

ul,
ol {
	margin: 0 0 2rem 0;
	padding: 0 0 0 1.5rem;
}

ul {
	list-style-type: disc;
}

ol {
	list-style-type: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1rem;
	padding-left: 1rem;
}

dt {
	font-weight: 600;
}

dd {
	margin: 0 2rem 2rem;
}

embed,
iframe,
object {
	max-width: 100%;
}

img {
	height: auto;
	max-width: 100%;
	vertical-align: middle;
}

figure {
	margin: 0 0 2rem;
}
figure img {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

figcaption {
	color: var(--color-grey-60);
	font-size: var(--font-size-875);
	margin-bottom: 0.5rem;
	margin-top: 0.5rem;
	text-align: center;
}

dfn,
cite,
em,
i {
	font-style: italic;
}

cite {
	color: var(--color-grey-60);
	display: block;
	margin-top: 1.5rem;
}

address {
	margin: 0 0 2rem;
}

abbr,
abbr[title],
acronym {
	border-bottom: none;
	cursor: help;
	text-decoration: underline dotted;
}

mark,
ins {
	background-color: rgba(var(--color-light-rgb), 0.3);
	text-decoration: none;
}

big {
	font-size: 134%;
}

pre {
	font-size: 1.125rem;
	line-height: 1.7777777778;
	background-color: rgba(var(--color-light-rgb), 0.3);
	font-family: "Courier 10 Pitch", "Courier", monospace;
	margin: 0 0 2rem;
	max-width: 100%;
	overflow: auto;
	padding: 2rem;
}

code,
kbd,
tt,
var {
	font-family: "Monaco", "Consolas", "Andale Mono", "DejaVu Sans Mono", monospace;
}

table {
	border-collapse: separate;
	border-spacing: 0;
	margin: 0 0 2rem;
	table-layout: fixed;
	width: 100%;
}

tr {
	vertical-align: top;
}

th,
td {
	padding: 0.5rem;
	text-align: left;
}

/*--------------------------------------------------------------
# Objects - Undecorated design patterns.
--------------------------------------------------------------*/
.animated {
	animation-duration: 300ms;
	animation-fill-mode: both;
}

@keyframes fadeInDown {
	from {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	to {
		opacity: 1;
		transform: translate3d(0, 0, 0);
	}
}
.fade-in-down {
	animation-name: fadeInDown;
}

.svg {
	display: inline-block;
	fill: currentColor;
	height: 1em;
	position: relative;
	top: -0.0625em;
	vertical-align: middle;
	width: 1em;
}

.flex {
	display: flex;
}

.flex-wrap {
	flex-wrap: wrap;
}

.justify-between {
	justify-content: space-between;
}

.justify-center {
	justify-content: center;
}

.items-center {
	align-items: center;
}

@supports (grid-area: auto) {
	.grid-footer {
		display: grid;
		grid-gap: 2rem;
		grid-template-columns: repeat(4, 1fr);
	}
	.grid-footer__logos, .grid-footer__credit, .grid-footer__info {
		grid-column: span 4;
	}
	@media screen and (min-width: 38em) {
		.grid-footer__logos, .grid-footer__credit {
			grid-column: span 2;
		}
	}
}
.pagination {
	padding-bottom: 4rem;
}

@media screen and (min-width: 48em) {
	.grid {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	.grid__main {
		width: 64%;
	}
	.grid__sidebar {
		width: 32%;
	}
	.grid .entry--card {
		width: 30%;
	}

	.grid-footer {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	.grid-footer__item {
		width: 30%;
	}

	.pagination {
		padding-bottom: 0;
	}

	@supports (grid-area: auto) {
		.grid {
			display: grid;
			grid-gap: 2rem;
			grid-template-columns: repeat(3, 1fr);
		}
		.grid__main {
			grid-column: span 2;
			width: auto;
		}
		.grid__sidebar {
			border-left: 1px solid;
			grid-column: span 1;
			padding-left: 1rem;
			width: auto;
		}
		.grid .entry--card {
			width: auto;
		}

		.grid-footer {
			display: grid;
		}
		.grid-footer__item {
			width: auto;
		}
		.grid-footer__logos, .grid-footer__credit {
			border-right: 1px solid;
			grid-column: span 1;
			padding-right: 1.5rem;
		}
		.grid-footer__info {
			grid-column: span 2;
		}
	}
}
.reset-list {
	margin: 0;
	padding: 0;
	list-style-type: none;
}

.max-width-1 {
	max-width: 66rem;
}

.max-width-2 {
	max-width: 44rem;
}

.max-width-3 {
	max-width: 40rem;
}

.mx-auto {
	margin-left: auto;
	margin-right: auto;
}

.reset-list {
	margin: 0;
	padding: 0;
	list-style-type: none;
}

/*--------------------------------------------------------------
# Blocks - editor related styles.
--------------------------------------------------------------*/
.wp-block-button {
	margin-bottom: 2rem;
}
.wp-block-button.aligncenter {
	text-align: center;
}
.wp-block-button.alignright {
	text-align: right;
}
.wp-block-button__link {
	background-color: var(--color-base);
	border: none;
	border-radius: 0;
	color: var(--color-white);
	padding: 0.5rem 1.5rem;
	text-decoration: none;
	display: inline-block;
}
.wp-block-button__link:visited {
	color: var(--color-white);
}
.wp-block-button__link:hover, .wp-block-button__link:focus, .wp-block-button__link:active {
	background-color: var(--color-dark);
	color: var(--color-white);
	outline-offset: -2px;
}
.is-style-outline .wp-block-button__link {
	background-color: transparent;
	border: 2px solid;
	color: var(--color-dark);
}
.is-style-outline .wp-block-button__link:hover, .is-style-outline .wp-block-button__link:focus, .is-style-outline .wp-block-button__link:active {
	background-color: var(--color-dark);
	border-color: var(--color-dark);
	color: var(--color-white);
}
.wp-block-button.is-style-squared {
	background-color: rgba(var(--color-light-rgb), 0.3);
	box-shadow: inset 0 0 0 4px rgba(var(--color-light-rgb), 0.3), inset 0 0 0 5px var(--color-white);
	padding: 1.5rem;
}
.wp-block-columns, .wp-block-text-columns {
	margin-bottom: 2rem;
}
.wp-block-columns .wp-block-column, .wp-block-text-columns .wp-block-column {
	margin: 0;
	width: auto;
}
.wp-block-columns .wp-block-column :last-child, .wp-block-text-columns .wp-block-column :last-child {
	margin-bottom: 0;
}
@supports (grid-area: auto) {
	@media (min-width: 48em) {
		.wp-block-columns, .wp-block-text-columns {
			display: grid;
			grid-gap: 1.5rem;
			grid-template-columns: repeat(2, 1fr);
		}
		.wp-block-columns.has-3-columns, .wp-block-columns.columns-3, .wp-block-text-columns.has-3-columns, .wp-block-text-columns.columns-3 {
			grid-template-columns: repeat(3, 1fr);
		}
		.wp-block-columns.has-4-columns, .wp-block-columns.columns-4, .wp-block-text-columns.has-4-columns, .wp-block-text-columns.columns-4 {
			grid-template-columns: repeat(4, 1fr);
		}
	}
}
.wp-block-cover-image {
	align-items: center;
	background-size: cover;
	display: flex;
	justify-content: center;
	margin-bottom: 2rem;
	min-height: 27rem;
	position: relative;
}
.wp-block-cover-image-text {
	font-size: var(--font-size-h1);
