.screen-reader-text {
	border: 0;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
	word-wrap: normal !important;
}
.screen-reader-text:focus {
	background-color: var(--color-base);
	clip: auto !important;
	clip-path: none;
	color: var(--color-white);
	display: block;
	font-size: var(--font-size-875);
	font-weight: 600;
	height: auto;
	left: 0.5rem;
	padding: 0.5rem 1rem;
	text-decoration: none;
	top: 0.5rem;
	width: auto;
	z-index: 100000;
}

/* stylelint-enable */
#main[tabindex="-1"]:focus {
	outline: 0;
}

.alignleft {
	float: left;
	margin-bottom: 1.5rem;
	margin-right: 1.5rem;
}

.alignright {
	float: right;
	margin-bottom: 1.5rem;
	margin-left: 1.5rem;
}

@media (min-width: 48em) {
	.alignleft,
.alignright {
		max-width: 50%;
	}
}
.aligncenter {
	display: block;
	margin-left: auto;
	margin-right: auto;
	clear: both;
}

.alignfull,
.alignwide {
	margin-left: -1rem;
	margin-right: -1rem;
	max-width: calc(100% + 2rem);
}
@media (min-width: 48em) {
	.alignfull,
.alignwide {
		margin-left: -2rem;
		margin-right: -2rem;
		max-width: calc(100% + 4rem);
	}
	.alignfull.wp-gallery-block,
.alignwide.wp-gallery-block {
		margin-left: -3rem;
		margin-right: -3rem;
		max-width: calc(100% + 6rem);
	}
}

@media (min-width: 48em) {
	.wp-block-gallery.alignwide {
		margin-left: -2.5rem;
		margin-right: -2.5rem;
		max-width: calc(100% + 5rem);
	}
}
